        description: 'In 1928 the german architect and philosopher Herman Sorgel proposed his project for an isolated and lowered mediterranean sea, named Atlantropa. His vision was to close the Strait of Gibraltar with a portentous hydroelectric dam; lock the Suez Canal to the Red Sea; and cut off the Black Sea through the Dardanelles.It was his belief that this international and collective endeavour would bring peace among all the participants once the appearance of new agricultural land came to fruition due to the lowering of the sea levels. Being the son of a hydroelectric engineer, Sorgel thought of Atlantropa as something more than a mere utopia: he studied the construction details and figures of the epic operation for 20 years, and never ceased to strive for its completion. Nearly a century after this meta-continental project was conceptualised, we can attempt to adopt his solution to question the geopolitical borders of contemporary Europe. Focusing on the Strait of Gibraltar, the so called first world is separated from the third world by a narrow strip of water: a rupture that is not only physical but overall cultural and political.This visual exercise aims at using Sorgels historical power dam at the entrance of the Mediterranean to re-question the idea of national and continental frontier between Europe and northern Africa. Could a geophysical border become an architecture to live, a threshold to dwell? Could a power dam become a buffer zone for cultural and social mixit?From Sorgels Atlantropa, to Le Corbusiers Plan Obus; from Constants New Babylon to Kenzo Tanges Tokyo Bay, the images propose a large infrastructure, prompted by XX centurys utopias and mega projects. A monument that is continuous in its political value and cultural bridging function',
